.wrapper {
    display: block;
    position: relative;
    margin: 0.6em 0;
    width: 100%;
    padding-left: 0.2em;
}

.element>input{
    display: block;
}

.element {
    appearance: none;
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    width: 2em;
    height: 2em;
    border: 1px solid var(--border);
    border-radius: 0.2em;
    background-color: var(--accent);
}

.element:checked{
    background-color: var(--accent-hover);
}

.element:checked::before{
    content: "";
    display: block;
    position: absolute;
    top: -0.3em;
    left: 0.4em;
    width: 0.6em;
    height: 1.2em;
    border: 0.3em solid transparent;
    border-bottom-color: var(--star-primary);
    border-right-color: var(--star-primary);
    transform-origin: 50% 50%;
    transform: rotate(35deg);
}

.wrapper>p {
    font-size: 1.4em;
    line-height: 1.2;
    color: var(--text-primary);
    margin-left: 1.4em;
    background-color: var(--accent);
}