* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background-color: var(--background);
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

a{
  text-decoration: none;
  color:inherit;
}

div{
  background-color: inherit;
}

.container{
  max-width: 1500px;
  margin: 0 auto;
  padding: 0;
  min-width: 400px;
}

:root {
  --background: #F6FFDE;
  --text-primary: #0F2103;
  --text-secondary: #183605;
  --accent: #E3F2C1;
  --accent-hover: #C9DBB2;
  --border: #1f1e1e;
  --shadow: #AAC8A7;
  --star-primary: #D80032;
  --star-secondary: #B0A695;
  --ScannerWidth:300px;
  --ScannerHeight:300px;
}


@media(hover: hover){
  button:hover {
    box-shadow: 0 0 10px var(--accent-hover), 0 0 10px var(--accent-hover), 0 0 10px var(--accent-hover), 0 0 10px var(--accent-hover),  0 0 10px var(--accent-hover) ;
    transform: scale(0.92);
  }
}

@media (hover:none){
  button:active {
    box-shadow: 0 0 10px var(--accent-hover), 0 0 10px var(--accent-hover), 0 0 10px var(--accent-hover), 0 0 10px var(--accent-hover),  0 0 10px var(--accent-hover) ;
    transform: scale(0.92);
  }
}