.qr_message {
    width: 100%;
    border: 1px solid var(--border);
    background-color: var(--accent);
    padding: 0.6em;
    border-radius: 0.6em;
    font-size: 1.2em;
    text-align: center;

    animation-name: show_message;
    animation-duration: 1s;
    animation-fill-mode: forwards;
    animation-timing-function:ease-in-out;

    position: relative;
    z-index: 1;

}


