.modal {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    /*transform: scale(0);*/
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;

    z-index: 100;
}

.modal.active {
    transform: scale(1);
    opacity: 1;
    pointer-events: all;
}

.modal__content {
    padding: 1em;
    border-radius: 0.8em;
    background-color: var(--accent);

    transform: scale(0.5);
    transition: 0.5s all;
    /*height: 50vh;*/
    /*max-width: 50vw;*/
    width: 50vw;
    margin: 0 auto;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0.5em;
}

.modal__content.active {
    transform: scale(1);
}

@media (max-width: 550px) {
    .modal__content {
        width: 90vw;
    }
}

/*====================*/
.modal input{
    color:var(--text-primary);
    border:1px solid var(--border);
    box-shadow: var(--shadow);
    margin-bottom: 0.5em;
    padding: 0.5em;
    border-radius: 0.5em;
    font-size: 1em;
}

