.wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 0.5em;
    margin-bottom: 0.5em;
    gap: 0.5em;
}
.wrapper button{
    border-radius: 50%;
    padding: 0.8em;
}

.wrapper input{
    width: 30%;
    font-size: 1.8em;
    text-align: center;
    border-radius: 0.2em;
    border: 1px solid var(--border);
}

