.wrapper {
}
.row{
    display: flex;
    /*justify-content: space-between;*/
    align-items: center;
    color:var(--text-secondary);
    border:1px solid var(--border);
    margin-bottom: 0.1em;
    border-radius: 0.5em;
    width: 100%;
    padding: 0.1em 0.2em 0em;

}
.row button{
    color:var(--star-primary);
    cursor: pointer;
}

.row:hover{
    box-shadow: 0 0 10px var(--accent-hover), 0 0 10px var(--accent-hover), 0 0 10px var(--accent-hover), 0 0 10px var(--accent-hover), 0 0 10px var(--accent-hover);
    background-color: var(--accent-hover);
    transform: scale(0.99);
    transition: all ease 300ms;
}
.title{
    display: flex;
    padding: 0 0.2em;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    border-radius: inherit;
    background-color: inherit;
}
.group{
    font-size: 1.2em;
    font-style: italic;
    font-weight: 600;
    color: var(--text-secondary);
    line-height: 1.8em;
    max-width: 80%;
    /*border-bottom: 1px solid var(--shadow);*/
    /*width: 100%;*/
    border-radius: 0.5em;
    margin-bottom: 0.2em;
    padding-left: 0.2em;

    display: flex;
    position: relative;

}
.total_price{
    color:var(--star-primary);
}
.group_more{
    font-size: 1em;
    font-style: italic;
    font-weight: 600;
    color: var(--text-secondary);
    line-height: 1.8em;
    /*max-width: 80%;*/
    /*border-bottom: 1px solid var(--shadow);*/
    width: 100%;
    border-radius: 0.5em;
    margin-bottom: 0.2em;
    padding-left: 0.2em;
    display: flex;
    justify-content: space-between;
}
.sub_title{
    padding-left: 1.2em;
}

.column{
    position: relative;
    width: 25%;
    border-radius: inherit;
    background-color: inherit;
}

.price{
    border-bottom:1px solid var(--border);
    padding: 0 0.2em;
    text-align: center;
    border-top-right-radius: inherit;
    background-color: inherit;
    font-size: 1.1em;
}
.stock{
    padding: 0 0.4em;
    border-bottom-right-radius: inherit;
    text-align: right;
    background-color: inherit;
    font-size: 0.9em;
}
.amount{
    position: absolute;
    top: 0;
    left: -0.6em;
    background-color: var(--star-secondary);
    border: 2px solid var(--star-primary);
    font-size: 1.5em;
    border-radius: 50%;
    padding: 0.15em;
    opacity: 0.8;
    color: var(--star-primary);
}

.marker{
    position: absolute;
    display: flex;
    top:-0.1em;
    right: -2.5em;
    opacity: 0.8;
}
.group_button{
    display: flex;
    gap:0.2em;
    justify-content: space-between;
}